@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Open Sans Condensed", sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  background-size: cover;
  background: linear-gradient(to top, #3f51b1 0%, #5a55ae 13%, #7b5fac 25%, #8f6aae 38%, #a86aa4 50%, #cc6b8e 62%, #f18271 75%, #f3a469 87%, #f7c978 100%);
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow-x: hidden;
}

code {
  font-family: "Open Sans Condensed", sans-serif;
}

/* Using RResponsive Typography through REM */

/* Media Queries for Font Size */

/* Iphone */

@media only screen and (min-width: 320px) {
  html {
    font-size: 10px;
  }
}

/* Extra Small Devices, Phones */

@media only screen and (min-width: 480px) {
  html {
    font-size: 11px;
  }
}

/* Small Devices, Tablets */

@media only screen and (min-width: 768px) {
  html {
    font-size: 13px;
  }
}

/* Medium Devices, Desktops */

@media only screen and (min-width: 992px) {
  html {
    font-size: 14px;
  }
}

/* Large Devices, Wide Screens */

@media only screen and (min-width: 1200px) {
  html {
    font-size: 15px;
  }
}

@media only screen and (min-width: 1400px) {
  html {
    font-size: 16px;
  }
}

@media only screen and (min-width: 1600px) {
  html {
    font-size: 17px;
  }
}

@media only screen and (min-width: 2000px) {
  html {
    font-size: 18px;
  }
}
.App {
    width: 100vw;
    height: 100vh;
}
.Navbar {
    width: 100%;
    height: 6rem;
    background-image: url(data:image/png;base64,);
    background-repeat: repeat-x;
    background-size: 6rem 6rem;
    text-align: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
}

a {
    color: black;
    text-decoration: none;
}

/* CSS for title logo on top left */

.titleContainer {
    height: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.titleContainer:hover {
    cursor: pointer;
    transition: all 0.3s ease 0s;
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(data:image/png;base64,);
}

.title {
    padding-right: 1rem;
}

.title>a {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    font-size: 4rem;
}

.title>a>img {
    width: 5rem;
    height: 5rem;
}

/* CSS for other nav items on the right */

.navContainer {
    height: 100%;
    text-align: center;
}

.navItems {
    text-align: center;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.navItems>li {
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.navItems>li:hover {
    cursor: pointer;
    transition: all 0.3s ease 0s;
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(data:image/png;base64,);
}

.navItems>li>a {
    font-size: 1.5rem;
    padding: 1.5rem;
}
/* Quizlet Component CSS */

.Quizlet {
    width: 100%;
    height: calc(100vh - 6rem);
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

/* Content Cotainer CSS */

.topContainer {
    width: auto;
    height: auto;
}

.botContainer {
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

/* Card Component CSS */

.Card {
    width: 30rem;
    height: 30rem;
    padding: 2rem;
    background-color: white;
    border-radius: 10%;
    text-align: center;
}

.Card>h1 {
    font-size: 3rem;
}

.Card>img {
    width: 18rem;
    height: 18rem;
}

.Card>h3 {
    font-size: 2rem;
}

/* Button Component CSS */

.Button>button {
    background-color: rgba(207, 194, 194, 0.781);
    width: 11rem;
    height: 5rem;
    font-size: 3rem;
    border-radius: 10%;
    box-shadow: .4rem .3rem .3rem black;
    border: black;
}

.Button>button:hover {
    transition: all 0.3s ease 0s;
    background-color: rgb(117, 113, 113);
    border-radius: 1%;
}
